import next from 'next';
// @ts-ignore
import cors from 'cors';
import express from 'express';
import { attachJssRenderingHostMiddleware } from './renderingHost/attachJssRenderingHostMiddleware';
import { attachDisconnectedServices } from './disconnectedService';
import {
    attachUniformServicesToServer,
    CreatePublishProviderFunc,
    parseUniformServerConfig,
    serverLogger,
} from '@uniformdev/common-server';
import { NextBuildAndExportEngine } from '@uniformdev/next-server';
import { getNextServerConfig } from '@uniformdev/next';

type JSSMode = 'connected' | 'disconnected';

interface CreateServerOptions {
    dev?: boolean;
    jssMode?: JSSMode;
    port?: number;
    serverUrl: string;
    createPublishProvider: CreatePublishProviderFunc;
}

export async function createUniformServer({
    dev = process.env.NODE_ENV !== 'production',
    jssMode = (process.env.JSS_MODE as JSSMode) || 'connected',
    port = 3000,
    serverUrl,
    createPublishProvider,
}: CreateServerOptions) {
    // Serve the local assets (css,js) from the serverUrl only
    // when in ssr mode, we don't want this for static export
    // this is needed for experience editor to request them
    // from the correct location
    process.env.UNIFORM_SSR_ASSET_PREFIX = serverUrl;

    const app = next({ dev });
    const defaultRequestHandler = app.getRequestHandler();

    await app.prepare();
    const server = express();
    server.use(cors());
    server.use(express.static('public'));

    attachJssRenderingHostMiddleware(server, app);

    if (jssMode === 'disconnected') {
        await attachDisconnectedServices(server, getNextServerConfig());
    }

    // Setup Uniform config and attach Uniform-specific middleware to the existing server.
    const uniformServerConfig = parseUniformServerConfig(process.env, serverLogger);
    const buildAndExportEngine = new NextBuildAndExportEngine();

    const options = {
        uniformServerConfig,
        createPublishProvider,
    };

    attachUniformServicesToServer(server, buildAndExportEngine, serverLogger, options);

    // We have to wrap defaultRequestHandler because it doesn't
    // have the same function signature as an express RequestHandler
    server.use((req: any, res) => defaultRequestHandler(req, res));

    server.listen(port, (err: Error) => {
        if (err) throw err;
        console.log(`> Ready on ${serverUrl}`);
    });
}
